/* Reser and base styles. */
*,
*::before,
*::after {
    padding: 0;
    margin: 0;
    border: none;
    box-sizing: border-box;

}

a,
a:visited {
    color: inherit;
    text-decoration: none;
}

a:hover {
    color: inherit;
    text-decoration: none;

}

a:focus,
a:active {
    outline: none;

}

/* Common styles. */

aside,
nav,
footer,
header,
section,
main {
    display: block;

}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    font-size: inherit;
    font-weight: inherit;
    line-height: inherit;

}

p+p {
    margin-top: 1em;


}

ul {
    list-style: none;
}

img,
svg {
    vertical-align: top;
    max-width: 100%;
    height: auto;
}


input,
select,
textarea,
button {
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;

}

input::-ms-clear {
    display: none;

}

button,
input[type="submit"] {
    display: inline-block;
    box-shadow: none;
    background-color: transparent;
    background: none;
    cursor: pointer;
}

button::-moz-focus-inner {
    padding: 0;
    border: 0;

}

label {
    cursor: pointer;
}

legend {
    display: block;
}